import React from 'react';
import { useNavigate } from 'react-router-dom';
import Swal from 'sweetalert2';

const Cart = ({ cart, onRemoveFromCart, onCheckout, onClose, currentUser }) => {
  const navigate = useNavigate();
  const cartTotal = cart.reduce((sum, item) => sum + item.price, 0).toFixed(2);

  const handleCheckout = () => {
    // Check if cart is empty first
    if (cart.length === 0) {
      Swal.fire({
        title: 'Cart is Empty',
        text: 'Please add items to your cart before checkout',
        icon: 'info'
      });
      return;
    }

    // Check if user is logged in
    if (!currentUser) {
      Swal.fire({
        title: 'Login Required',
        text: 'Please login or register to place your order',
        icon: 'warning',
        showCancelButton: true,
        confirmButtonText: 'Login',
        cancelButtonText: 'Register',
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#28a745',
      }).then((result) => {
        if (result.isConfirmed) {
          // Go to login page
          navigate('/login');
          onClose();
        } else if (result.dismiss === Swal.DismissReason.cancel) {
          // Go to register page
          navigate('/register');
          onClose();
        }
      });
    } else {
      // User is logged in, show confirmation before placing order
      Swal.fire({
        title: 'Confirm Your Order',
        html: `
          <div style="text-align: left; padding: 10px;">
            <p><strong>Customer:</strong> ${currentUser.name}</p>
            <p><strong>Contact:</strong> ${currentUser.emailOrMobile}</p>
            <hr style="margin: 15px 0;">
            <p><strong>Total Items:</strong> ${cart.length}</p>
            <p><strong>Total Amount:</strong> <span style="color: #28a745; font-size: 1.2em;">$${cartTotal}</span></p>
          </div>
        `,
        icon: 'question',
        showCancelButton: true,
        confirmButtonText: 'Place Order',
        cancelButtonText: 'Cancel',
        confirmButtonColor: '#28a745',
        cancelButtonColor: '#d33',
      }).then((result) => {
        if (result.isConfirmed) {
          // Place the order
          Swal.fire({
            title: 'Order Placed Successfully!',
            html: `
              <div style="text-align: center; padding: 10px;">
                <p>Thank you, <strong>${currentUser.name}</strong>!</p>
                <p style="color: #28a745; font-size: 1.1em; margin: 15px 0;"> Your order has been confirmed</p>
                <p><strong>Order Total: $${cartTotal}</strong></p>
                <hr style="margin: 15px 0;">
                <p style="font-size: 0.9em;">Order confirmation has been sent to:<br/>${currentUser.emailOrMobile}</p>
              </div>
            `,
            icon: 'success',
            confirmButtonText: 'Continue Shopping',
            confirmButtonColor: '#3085d6'
          }).then(() => {
            onCheckout();
            onClose();
          });
        }
      });
    }
  };

  return (
    <div className="cart-overlay">
      <div className="cart-container">
        <div className="cart-content">
          <div className="cart-header">
            <h2>Shopping Cart</h2>
            <button className="close-btn" onClick={onClose}></button>
          </div>
          {cart.length === 0 ? (
            <p className="empty-cart">Your cart is empty</p>
          ) : (
            <>
              {cart.map((item, idx) => (
                <div key={idx} className="cart-item">
                  <img src={item.image} alt={item.name} className="cart-item-image" />
                  <div className="cart-item-details">
                    <div className="cart-item-header">
                      <h4>{item.name}</h4>
                      <button className="remove-btn" onClick={() => onRemoveFromCart(idx)}>
                         Remove
                      </button>
                    </div>
                    <p className="cart-item-category">{item.category}</p>
                    <p className="cart-item-price">${item.price}</p>
                  </div>
                </div>
              ))}
              <div className="cart-footer">
                <div className="cart-total">
                  <h3>Total: ${cartTotal}</h3>
                  {currentUser && (
                    <p className="cart-user-info">Ordering as: <strong>{currentUser.name}</strong></p>
                  )}
                </div>
                <button className="checkout-btn" onClick={handleCheckout}>
                  {currentUser ? 'Place Order' : ' Login to Checkout'}
                </button>
                <button className="continue-btn" onClick={onClose}>Continue Shopping</button>
              </div>
            </>
          )}
        </div>
      </div>
    </div>
  );
}

export default Cart;